import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Conocimiento } from '../models/conocimientoModel';
import { Proyecto } from '../models/proyectoModel';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  path: string = 'https://portfolio-app-jr.herokuapp.com/api/';

  constructor(private _http: HttpClient) { }

  getConocimientos() {
    return this._http.get<Observable<Conocimiento[]>>(this.path + 'conocimientos')
  }

  getProyectos() {
    return this._http.get<Observable<Proyecto[]>>(this.path + 'proyectos')
  }
}
