import { Component, OnInit } from '@angular/core';
import { Conocimiento } from 'src/app/models/conocimientoModel';
import { Proyecto } from 'src/app/models/proyectoModel';
import { ApiService } from 'src/app/services/api.service';

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.scss']
})
export class MainPageComponent implements OnInit {


  conocimientos: Conocimiento[] = [];

  proyectos: Proyecto[] = [];
  
	blog: any[] = [
    {
      titulo: "novedades ES6",
      img: "https://culture-lab.es/wp-content/uploads/2020/08/java-script.jpg",
      descripcion: 'Some example text.'
    },
    {
      titulo: "nodeJS & Sockets.io",
      img: "https://s3-us-west-2.amazonaws.com/devcodepro/media/tutorials/instalacion-de-nodejs-en-ubuntu-t1.jpg",
      descripcion: 'Some example text.'
    },
    {
      titulo: "Diseño DB mySQL",
      img: "data:image/png;base64,",
      descripcion: 'Some example text.'
    }
  ]
  
  constructor(private apiS: ApiService) {
  
   }

   ngOnInit(): void {
     this.apiS.getConocimientos().subscribe( (resp: any) => {
       this.conocimientos = resp.listaConocimientos;
     });

     this.apiS.getProyectos().subscribe( (resp: any) => {
      this.proyectos = resp.proyectosLista;
     })
  }

  scroll():void {
    this.mostrarTitulos();
    this.mostrarRows();
    this.mostrarColsdr();
    this.mostrarColsIz();
    this.parallaxEffect();
    this.mostrarProyectos();
  }

// Mostrar tíulos de manera que parezca que se están escribiendo
  mostrarTitulos():void {
    let titulos = document.querySelectorAll('.titulo');
    let spans = document.querySelectorAll(".spantitle");
    
    for(let i = 1; i < titulos.length; i++) {
      if(titulos[i].getClientRects()[0].top < 600 && titulos[i].getClientRects()[0].top > 0) {
        
        titulos[i].classList.add('displayOpacity')
        spans[i].classList.add('animationMaquina')
      } else if(titulos[i].getClientRects()[0].top > 600 || titulos[i].getClientRects()[0].top < -600){
        titulos[i].classList.remove('displayOpacity')
        spans[i].classList.remove('animationMaquina')
      }
    }
    
  }
// Mostrar las filas del DOM según se va scrolleando
  mostrarRows():void {
    let rows = document.querySelectorAll('.row');

    for (let i = 0; i < rows.length; i++) {
      if(rows[i].getClientRects()[0].top < 600 && rows[i].getClientRects()[0].top > -500) {
        rows[i].classList.add('displayOpacity');
      } 
    }
  }

  mostrarColsdr() {
    let rows: any = document.querySelectorAll('.col-dr');

    for (let i = 0; i < rows.length; i++) {
      if(rows[i].getClientRects()[0].top < 600 && rows[i].getClientRects()[0].top > 0) {
        rows[i].classList.add('conocimiento');
        rows[i].style.opacity = 1;
      } else if(rows[i].getClientRects()[0].top > 600 || rows[i].getClientRects()[0].top < -600) {
        rows[i].classList.remove('conocimiento');
        rows[i].style.opacity = 0;
      } 
    }
  }

  mostrarColsIz() {
    let rows: any = document.querySelectorAll('.col-iz');

    for (let i = 0; i < rows.length; i++) {
      if(rows[i].getClientRects()[0].top < 600 && rows[i].getClientRects()[0].top > 0) {
        rows[i].classList.add('animacionMostrarDerecha');
        rows[i].style.opacity = 1;
      } else if(rows[i].getClientRects()[0].top > 600 || rows[i].getClientRects()[0].top < -600) {
        rows[i].classList.remove('animacionMostrarDerecha');
        rows[i].style.opacity = 0;
      } 
    }
  }

  mostrarProyectos():void {
    let cards = document.querySelectorAll('.card');

    for (let i = 0; i < cards.length; i++) {
      if(cards[i].getClientRects()[0].top < 600 && cards[i].getClientRects()[0].top > -500) {
        if(i < 3) {cards[i].classList.add(`delay${i + 1}`); }
        cards[i].classList.add('displayOpacity');
      } else {
        if(i < 3) {cards[i].classList.remove(`delay${i + 1}`); }
        cards[i].classList.remove('displayOpacity');
      }
    }
  }

  //parallax scroll

  parallaxEffect() {
    let value = window.scrollY;

    
    
    let bg: HTMLElement | null = document.getElementById('bg');     
    let moon: HTMLElement | null = document.getElementById('moon');    
    let mountain: HTMLElement | null = document.getElementById('mountain'); 
    let road: HTMLElement | null = document.getElementById('road');    
    let text: HTMLElement | null = document.getElementById('text');    
    
   
    if(window.screen.width < 500) {
      bg!.style.top = (value - 2800) * 0.20 + 'px';
      moon!.style.left = (value - 3200) * 1 + 'px';
      moon!.style.top = (value - 2500) * 1 + 'px';
      mountain!.style.top = (value - 2800) * 0.15 + 'px';
      road!.style.top = (value - 2800) * 0.15 + 'px';
      text!.style.top = (value - 2800) * 0.60 + 'px';
    }else if(window.screen.width < 990 && window.screen.width > 500) {
      bg!.style.top = (value - 2800) * 0.20 + 'px';
      moon!.style.left = (value - 4000) * 1 + 'px';
      moon!.style.top = (value - 2900) * 1 + 'px';
      mountain!.style.top = (value - 2800) * 0.15 + 'px';
      road!.style.top = (value - 2800) * 0.15 + 'px';
      text!.style.top = (value - 3000) * 0.8 + 'px';
    } else {
      bg!.style.top = (value - 2600) * 0.30 + 'px';
      moon!.style.left = (value - 2600) * 1 + 'px';
      moon!.style.top = (value - 2000) * .7 + 'px';
      mountain!.style.top = (value - 2000) * 0.15 + 'px';
      road!.style.top = (value - 2000) * 0.15 + 'px';
      text!.style.top = (value - 2200) * 0.4 + 'px';

    }
    
  


  }


}
