import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.scss']
})
export class VideoComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  mostrarSkills() {
    let skills = document.querySelectorAll('.skill') ;

    for (let i = 0; i < skills.length; i++) {
      if(skills[i].getClientRects()[0].top < 600 && skills[i].getClientRects()[0].top > -500) {
        setTimeout(() => {
          skills[i].classList.add('dispOpacity');
        }, (i + 1) * 200);
      } else {
        skills[i].classList.remove('dispOpacity');
      }
    }
  }


}
